/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Ubuntu", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	margin: 0.5rem 0;
	font-family: "Montserrat", sans-serif;
}

h1 {
	font-size: 2.5rem;
}

h2 {
	font-size: 2rem;
}

h3 {
	font-size: 1.5rem;
}

h4 {
	font-size: 1.5rem;
}

p {
	margin-bottom: 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #4169e1;
	text-decoration: none;
}
a:visited {
	color: #4169e1;
}
a:hover, a:focus, a:active {
	color: #191970;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	max-width: 2200px;
	margin: 0 auto;
	overflow: hidden;
	display: grid;
	grid-template-columns: 1fr minmax(0, 1000px) 1fr;
	grid-template-areas: "header header header" ". main ." ". sidebar ." "footer footer footer";
}

.site-main {
	padding: 0 1rem;
}

.widget-area {
	padding: 1rem;
}

.site-header {
	grid-area: header;
}

.site-main {
	grid-area: main;
}

.widget-area {
	grid-area: sidebar;
}

.page-template-left-sidebar .widget-area {
	padding: 3rem 3rem 1rem 1rem;
}

.site-footer {
	grid-area: footer;
}

/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Header
--------------------------------------------- */
.site-header {
	background-color: #111;
	color: #fff;
}

.site-header a {
	color: #fff;
}

.site-branding {
	padding: 1rem;
	display: flex;
	align-items: center;
}

.site-title {
	font-size: 1.875rem;
	margin: 0;
}

.site-description {
	margin: 0;
}

@media screen and (min-width: 50em) {
	.site-branding {
		text-align: center;
	}

	@supports (grid-area: auto) {
		.site-header {
			display: grid;
			grid-template-columns: 1fr minmax(auto, 25rem) minmax(auto, 37.5rem) 1fr;
			justify-content: center;
		}

		.site-branding {
			grid-column: 2/3;
			text-align: left;
		}
	}
}
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
	position: absolute;
	top: 6.5rem;
	right: 0;
	z-index: 1000;
}
.main-navigation svg {
	fill: #fff;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	background-color: #111;
}

.main-navigation li {
	position: relative;
}

.main-navigation li.current-menu-item > a,
.main-navigation li.current_page_item > a,
.main-navigation li:hover > a,
.main-navigation li.focus > a {
	text-decoration: underline;
	text-decoration-color: #4169e1;
}

.main-navigation li.current-menu-item svg path,
.main-navigation li.current_page_item svg path,
.main-navigation li:hover > a svg path,
.main-navigation li.focus > a svg path {
	fill: #4169e1;
}

.main-navigation a {
	display: block;
	padding: 0.75rem 1.25rem;
	text-align: center;
}

.main-navigation.toggled ul {
	display: block;
}

.menu-toggle {
	position: absolute;
	top: -4.3rem;
	right: 0.5rem;
	z-index: 1100;
	display: block;
	width: 3.75rem;
	padding: 1rem;
	margin: 0;
	border: none;
	background: transparent;
	color: #fff;
}

@media screen and (min-width: 50em) {
	.menu-toggle {
		display: none;
	}

	.main-navigation {
		width: auto;
		position: relative;
		top: 0;
		align-self: center;
		margin-left: auto;
	}

	.main-navigation ul,
.main-navigation.toggled ul {
		display: flex;
		justify-content: center;
	}

	.main-navigation ul ul {
		box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
		float: left;
		position: absolute;
		top: 100%;
		left: -999em;
		z-index: 99999;
		background-color: #111;
		flex-direction: column;
	}

	.main-navigation ul ul a {
		left: -999em;
		top: 0;
		width: 200px;
		text-align: left;
	}

	.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
		left: 100%;
		top: 0;
	}

	.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
		left: auto;
	}
}
/* If Admin Bar is on front end */
.admin-bar .main-navigation {
	top: calc(5rem + 43px);
}

@media screen and (min-width: 50em) {
	.admin-bar .main-navigation {
		top: 0;
	}
}
/* Comments & Paging Navigation */
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}

.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.cat-links,
.tags-links,
.comments-link {
	padding-right: 1rem;
}

/* Home page styles */
.page-id-6 h1 {
	margin-top: 2rem;
	margin-bottom: 1.5rem;
}
.page-id-6 .recent-news {
	text-align: center;
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	justify-content: space-around;
	margin-bottom: 1.5rem;
}
.page-id-6 .recent-news img {
	display: block;
}
.page-id-6 .recent-news > a {
	position: relative;
	display: block;
	max-width: 300px;
	margin: 0 0.5rem 1rem;
}
.page-id-6 .recent-news h2 {
	justify-self: start;
	width: 100%;
	margin-bottom: 1rem;
}
.page-id-6 .recent-news h3 {
	position: absolute;
	left: 0;
	right: 0;
	font-size: 1.2rem;
	bottom: 0;
	padding: 1rem;
	margin: 0;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.8);
}
.page-id-6 .recent-news p {
	width: 100%;
	margin: 1rem;
}
.page-id-6 .recent-news a.all-news-link {
	display: block;
	font-weight: 700;
}

